body,html{
  height: 100%;
  margin: 0;
}

#root{
  height: 100%;
  background-image: url("https://bnetcmsus-a.akamaihd.net/cms/gallery/6V1SW4QDOB4W1539119599198.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}
.App{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}
.firstpage{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

}
.userHeadline{
  margin: 0;
  height: 150px;
  width: 800px;
  text-align: center;
}
.userHeadline h1 {
  color:#fff;
  font-size: 60px;
  opacity: 0.7;
  font-family: 'UnifrakturCook', cursive;

}
.userContainer{
  margin: 0;
  height: 400px;
  width: 800px;
  display: flex;
}
.hordeContainer{
  cursor: pointer;
  height: 400px;
  width: 400px;
  margin: 0;
  background-image: url("https://seeklogo.net/wp-content/uploads/2013/06/world-of-warcraft-horde-vector-logo.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: all .5s ease-in-out; 
}
.hordeContainer:hover{
  transform: scale(1.2);
}
.allianceContainer{
  cursor: pointer;
  height: 400px;
  width: 400px;
  margin: 0;
  background-image: url("https://c1-ebgames.eb-cdn.com.au/merchandising/images/packshots/e6bca696c4fb4a3badc8d57cf1f3ab78_Large.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: all .5s ease-in-out; 
}
.allianceContainer:hover{
  transform: scale(1.2);
}
.userInfo{
  height: 320px;
  width: 195px;
  margin-top: -185px;
  border-radius: 15%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: all .5s ease-in-out; 
  opacity: 0.3;
} 
.user2Info{
  height: 320px;
  width: 195px;
  margin-top: -185px;
  border-radius: 15%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: all .5s ease-in-out; 
  opacity: 0.3;
}

.active{
  opacity: 1;
  transform: scale(1.2)
}

.Gameboard{
  height: 600px;
  width: 1000px;
  display: flex;
  border-radius: 10%;
}
.row{
height: 100%;
width: 20%;
display: flex;
flex-direction: column-reverse;
justify-content: center;
align-items: center;
z-index: 1;
}
.transparent{
  position: absolute;
  height: 600px;
  width: 1000px;
  background: rgba(0,0,0,0.3);
  opacity: 0.5;

}
.circle{
  border-radius: 50%;
  background: rgba(0,0,0,0.4);
  width: 70%;
  height: 14%;
  margin: 7px;
  -webkit-justify-content: center;
  justify-content: center;
}
.horde {
  background-image: url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/4506f6b3-e68a-4724-993c-7f585bcdb0c3/d6fdvrk-7fc0990e-e9d2-4994-9242-a8e50b10cbda.png?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..-p6SzM2q_HczphbriJAA0VJIHj-g8dGOZWP7MBoiOrs");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.alliance{
  background-image: url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/d099d316-98cf-4095-a2f9-32cca4219baa/dam5zdb-54a8509e-0aa6-43aa-92ca-56a917596d4c.png?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..pIG8oY5ldYlgvBFfg05NDdTKd4TH_IAoUY41Dbj9Gmk");
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: 60%;
}
.gamemode{
  height: 400px;
  width: 400px;
  margin: 0;
  display:flex;
  justify-content: center;
  align-items: center;
  font-family: 'UnifrakturCook', cursive;
}

.gamemode h1{
  color: #fff;
  padding: 0;
  margin: 0;
  font-size: 60px;
  cursor: pointer;
  transition: all .5s ease-in-out; 
}

.gamemode h1:hover{
  transform: scale(1.2);
}
.overlay{
  height: 100vh;
  width: 100vw;
  position: fixed;
  background-color: rgba(0,0,0,0.4);
  display: flex;
  z-index: 1000;
  align-items: center;
  justify-content: center;
}

.winner-container{
  box-sizing: border-box;
  padding:30px;
  background-image: url("https://i.pinimg.com/originals/dc/ef/f6/dceff6eb8639f46f68f25c36125bdbeb.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 500px;
  width: 800px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.winner-text{
  font-family: 'UnifrakturCook', cursive;
  opacity: 0.7;
}
.fun-facts{
  font-family: 'UnifrakturCook', cursive;
  font-size: 20px;
  opacity: 0.7;
}
.body-button{
  margin-top: 10px;
  font-weight: 100;
  font-size: 30px;
  padding: 5px 26px;
  font-family: 'UnifrakturCook', cursive;
  border-radius: 10px;
  border: none;
  background: none;
  text-decoration: none;
  cursor: pointer;
}
.winner-container-buttons{
  margin-top: 60px;
  width: 400px;
  justify-content: space-between;
  display: flex;
}